import React from 'react';
import config from 'config';
import mapValues from 'lodash/mapValues';
import TextNodeType from 'classes/node-types/TextNodeType';
import { IToggleableState, ToggleableDefaultsType } from 'plugins/toggleable';

export interface IPlugin {
  // defaults: ToggleableDefaultsType;
  // displayValue: (plugin: IPlugin) => string;
  id: string;
  // onUpdate: <T extends IPlugin>(plugin: T, props: any) => void;
  // propertyName: string;
  // propertyUnit: string;
  // state: IToggleableState;
  title: string;
  dataAttributeName: string;
  initialState: object;
  // nodes: ?NodeList<HTMLElement>;
  // nodeTypes: TextNodeType;
  onMount(): void;
}

export interface BasePluginParamsType {
  id: string;
  title: string;
  // onUpdate: <T extends IPlugin>(plugin: T, props: any) => void;
}

export default class BasePlugin implements IPlugin {
  get dataAttributeName(): string {
    return `data-${config.widgetId}-original-${this.id}`;
  }

  get initialState() {
    return {
      id: this.id
    };
  }

  public static actions = {};

  public static mapDispatchToProps = (dispatch: any, props: any) => {
    return mapValues(BasePlugin.actions, (action: (arg0: any) => any) => {
      return () => dispatch(action(props.id));
    });
  };

  public static mapStateToProps = (state: any, { id }: any) => {
    // Extract reducers from combined state.
    const { plugins } = state.plugins;
    const statePlugin = plugins.find((plugin: { id: any }) => plugin.id === id);

    return { current: statePlugin.current };
  };
  public id: string;
  public title: string;
  // public onUpdate: <T extends IPlugin>(plugin: T, props: any) => void;

  constructor(params: BasePluginParamsType) {
    this.id = params.id;
    this.title = params.title;
    // this.onUpdate = params.onUpdate;
  }

  public onMount() {}
}
